import uniCalendar from "@/components/uni-calendar/uni-calendar.vue";
import uniSearchBar from '@/components/uni-search-bar/uni-search-bar.vue'
	export default {
		components: {
			uniCalendar,
			uniSearchBar
		},
		data() {
			return {
				title: "Hello"
			};
		},
		onLoad() {
			// 获取用户权限信息
			uni.getSetting({
				success: res => {
					// console.log(res.authSetting['scope.userInfo'])
					if (!res.authSetting['scope.camera']) {
						console.log('[]')
						uni.authorize({
							scope: 'scope.camera',
							success: (res) => {
								console.log(res)
							}
						})
					}
				}
			})
		},
		methods: {
			change(e) {
				console.log(e);
			},
			search(e) {
				console.log(e)
			}
		}
	};
